.App {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.card {
  background: white;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.card-title {
  font-size: 20px;
  font-weight: 500;
  color: #1d1d1f;
  margin: 0;
}

.add-card {
  background: white;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.add-card:hover {
  background: #f5f5f7;
}

.add-icon {
  font-size: 24px;
  color: #0071e3;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #1d1d1f;
  font-size: 14px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px;
  border: 1px solid #d2d2d7;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.2s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #0071e3;
}

.submit-button {
  background: #0071e3;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.submit-button:hover {
  background: #0077ed;
}

.App h1 {
  color: #333;
  margin-bottom: 30px;
}

.apple-card-wrapper {
  max-width: 100vw;
  margin: 0 auto;
  width: 100%;
  padding: 0;
  box-sizing: border-box;
}

.apple-card {
  max-width: 340px;
  width: 100%;
  margin: 0 auto 20px auto;
  padding: 20px 16px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 12px;
  border: 1px solid #d2d2d7;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.apple-create-btn {
  display: block;
  width: 100%;
  margin: 32px 0 0 0;
  padding: 16px 0;
  background: #0071e3;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  border: none;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  cursor: pointer;
  transition: background 0.2s;
  box-sizing: border-box;
}

.apple-create-btn:hover {
  background: #005bb5;
}

.apple-create-btn--centered {
  display: block;
  width: 200px;
  margin: 32px auto 0 auto;
  padding: 16px 0;
  background: #0071e3;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  border: none;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  cursor: pointer;
  transition: background 0.2s;
  box-sizing: border-box;
}

.apple-create-btn--centered:hover {
  background: #005bb5;
}

@media (max-width: 600px) {
  .App {
    padding: 0;
    margin: 0;
    min-width: 100vw;
    max-width: 100vw;
  }
  .apple-card-wrapper {
    max-width: 100vw;
    margin: 0;
    padding: 24px 0 0 0;
  }
  .apple-card {
    padding: 20px 16px;
    max-width: 340px;
    margin: 0 auto 20px auto;
  }
  .top-menu {
    margin-left: 0;
    margin-right: 0;
    width: 100vw;
    min-width: 100vw;
    justify-content: center;
  }
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.25);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.modal-window {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.18);
  margin-top: 24px;
  width: 100%;
  max-width: 400px;
  padding: 28px 20px 20px 20px;
  box-sizing: border-box;
}

@media (max-width: 600px) {
  .modal-window {
    margin-top: 0;
    border-radius: 0 0 16px 16px;
    max-width: 100vw;
    width: 100vw;
    min-height: 220px;
    padding: 24px 10px 16px 10px;
  }
}

.modal-window input {
  font-size: 18px;
  padding: 14px 12px;
}

.modal-window label {
  font-size: 15px;
}

.top-tabs {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-bottom: 28px;
  margin-top: 10px;
}

.top-tab {
  padding: 10px 22px;
  border: none;
  background: #f5f5f7;
  color: #222;
  font-size: 16px;
  border-radius: 16px 16px 0 0;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s, color 0.2s;
}

.top-tab.active {
  background: #0071e3;
  color: #fff;
}

.top-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 18px 0 28px 0;
  gap: 0;
}

.menu-arrow {
  background: none;
  border: none;
  font-size: 28px;
  color: #0071e3;
  cursor: pointer;
  padding: 0 18px;
  transition: color 0.2s;
  user-select: none;
}
.menu-arrow:active {
  color: #005bb5;
}

.menu-title {
  flex: 1 1 auto;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  color: #1d1d1f;
  letter-spacing: -0.01em;
} 